.newFileUpload .contentWrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  background-color: rgba(34, 100, 144, 0.3);
  border-radius: 0.5em;
  border-top-left-radius: 0;
  justify-content: center;
  padding: 5%;
  overflow-y: scroll; }
  .newFileUpload .contentWrapper .dropZone {
    border-radius: 0.5em;
    width: 100%;
    height: 100%; }
  .newFileUpload .contentWrapper .activeDropZone .chooseFilesWrapper {
    background-color: #225f88;
    border-radius: 0.5em;
    opacity: 0.5; }
  .newFileUpload .contentWrapper .acceptDropZone .chooseFilesWrapper {
    border: 2px dashed #45b39c;
    border-radius: 0.5em; }
  .newFileUpload .contentWrapper .rejectDropZone .chooseFilesWrapper {
    border: 2px dashed #ba4542;
    border-radius: 0.5em; }

.newFileUpload .full {
  padding: 0px;
  background-color: none; }

.newFileUpload .chooseFilesWrapper {
  border: 1px dashed white;
  border-radius: 1em;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white; }
  .newFileUpload .chooseFilesWrapper svg {
    width: 80px;
    height: 80px; }
    .newFileUpload .chooseFilesWrapper svg .a {
      fill: #b5885b !important; }
    .newFileUpload .chooseFilesWrapper svg .b {
      fill: #fff !important; }
    .newFileUpload .chooseFilesWrapper svg .c {
      fill: #e7eced !important; }
    .newFileUpload .chooseFilesWrapper svg .d {
      fill: #c7cac7 !important; }
    .newFileUpload .chooseFilesWrapper svg .e {
      fill: #f7b563 !important; }
    .newFileUpload .chooseFilesWrapper svg .f {
      fill: #32ba7c !important; }
  .newFileUpload .chooseFilesWrapper .messageWrapper {
    margin: 20px; }
    .newFileUpload .chooseFilesWrapper .messageWrapper .messageHeader {
      margin-bottom: 12px; }
  .newFileUpload .chooseFilesWrapper .fileSizeMessage {
    opacity: 0.7; }
  .newFileUpload .chooseFilesWrapper .inputWrapper {
    width: 256px;
    border-radius: 0.5em;
    height: 38px;
    margin-bottom: 38px;
    margin-top: 38px;
    background-color: white; }
    .newFileUpload .chooseFilesWrapper .inputWrapper input {
      opacity: 0;
      height: 100%;
      width: 100%; }
    .newFileUpload .chooseFilesWrapper .inputWrapper .inputLabel {
      color: #1e557b;
      position: relative;
      top: -30px;
      left: 80px;
      cursor: default;
      pointer-events: none; }

.newFileUpload .fileSummaryListWrapper {
  background-color: rgba(34, 100, 144, 0.3);
  flex-grow: 1;
  width: 100%;
  border-radius: 3.5%;
  border-top-left-radius: 0px;
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 25px;
  overflow-y: scroll; }
  .newFileUpload .fileSummaryListWrapper .row {
    margin: 0px;
    height: 22px;
    display: flex;
    flex-direction: row;
    margin-bottom: 18px; }
    .newFileUpload .fileSummaryListWrapper .row .fileSize {
      opacity: 0.5;
      margin-left: auto; }
    .newFileUpload .fileSummaryListWrapper .row .oversize {
      opacity: 1; }
    .newFileUpload .fileSummaryListWrapper .row .removeButton {
      opacity: 0.5;
      border-radius: 100%;
      background: none;
      color: white;
      border: 1px solid white;
      margin-left: 20px;
      min-width: 22px;
      min-height: 22px;
      height: 22px;
      width: 22px; }
  .newFileUpload .fileSummaryListWrapper .invalid {
    color: #fada6f; }
  .newFileUpload .fileSummaryListWrapper .header .headerLabel {
    font-weight: bold; }
  .newFileUpload .fileSummaryListWrapper .header .totalLabel {
    margin-left: auto;
    opacity: 0.5; }

.newFileUpload .footerButtons {
  align-self: flex-end;
  width: 100%;
  height: 10%;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-end; }
  .newFileUpload .footerButtons .buttons .bp3-button {
    background: unset;
    box-shadow: none;
    font-family: 'Roboto', sans-serif;
    width: 126px;
    height: 37.2px;
    margin-right: 12px;
    font-weight: bold;
    font-size: 12px; }
  .newFileUpload .footerButtons .buttons .cancelButton {
    background-color: #1e557b;
    color: white; }
  .newFileUpload .footerButtons .buttons .uploadButton {
    background-color: white;
    color: #1e557b; }
  .newFileUpload .footerButtons .bp3-disabled {
    opacity: 0.3; }
