        fill: #f8d578 !important; }
      .loginPageRoot .loginIcon svg .c {
        fill: #d4b053 !important; }
      .loginPageRoot .loginIcon svg .d {
        fill: #e4e7e7 !important; }
      .loginPageRoot .loginIcon svg .e {
        fill: #324d5b !important; }
      .loginPageRoot .loginIcon svg .f {
        fill: #cdcfcf !important; }
