.excelPreviewWrapper {
  display: flex;
  flex-direction: column;
  background-color: white;
  border-radius: 10px;
  flex-direction: column;
  padding-top: 5%;
  flex: 1;
  min-width: 0;
  overflow-x: scroll;
  max-width: 100%; }
  .excelPreviewWrapper .excelPreviewControls {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 3.5%; }
    .excelPreviewWrapper .excelPreviewControls .selectSheet {
      width: 200px; }
      .excelPreviewWrapper .excelPreviewControls .selectSheet .ant-select-selection-selected-value {
        opacity: 0.4 !important; }
    .excelPreviewWrapper .excelPreviewControls .sheetButtons {
      display: flex;
      flex-direction: row; }
      .excelPreviewWrapper .excelPreviewControls .sheetButtons .sheetButton {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        border: 1px solid #808495;
        width: 32px;
        height: 32px;
        opacity: 0.4; }
        .excelPreviewWrapper .excelPreviewControls .sheetButtons .sheetButton .sheetLabel {
          color: #808495;
          user-select: none; }
      .excelPreviewWrapper .excelPreviewControls .sheetButtons .sheetButton:hover {
        opacity: 1;
        cursor: pointer; }
      .excelPreviewWrapper .excelPreviewControls .sheetButtons .prevSheetButton {
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px; }
      .excelPreviewWrapper .excelPreviewControls .sheetButtons .nextSheetButton {
        border-left: none;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px; }
  .excelPreviewWrapper .excelPreviewData {
    flex-grow: 1;
    overflow: scroll; }
    .excelPreviewWrapper .excelPreviewData .data-grid-container .data-grid tr {
      min-height: 12px;
      max-height: 32px;
      display: flex;
      padding-top: 5px;
      border: none;
      border-bottom: 1px solid rgba(128, 132, 149, 0.2); }
      .excelPreviewWrapper .excelPreviewData .data-grid-container .data-grid tr .cell:hover {
        font-weight: 500; }
    .excelPreviewWrapper .excelPreviewData .data-grid-container .data-grid tr:hover {
      background-color: #f4f4f4; }
    .excelPreviewWrapper .excelPreviewData .data-grid-container .data-grid .cell {
      overflow-x: hidden;
      border: none;
      color: #808495;
      font-size: 12px;
      border-left: none;
      border-right: none;
      vertical-align: middle;
      text-align: left;
      padding-left: 20px;
      width: 90px;
      min-width: 90px;
      max-width: 90px; }
