.topNavigationRoot {
  background-color: #1e557b;
  display: flex;
  flex-direction: row;
  padding-left: 40px;
  padding-right: 22px;
  align-items: center;
  min-height: 52px;
  max-height: 52px;
  padding-top: 21px;
  padding-bottom: 21px; }
  .topNavigationRoot .appLogo {
    width: 98px;
    height: 34px;
    margin-right: 15px; }
    .topNavigationRoot .appLogo svg {
      fill: white !important; }
      .topNavigationRoot .appLogo svg .cls-1 {
        fill: #fff !important; }
      .topNavigationRoot .appLogo svg .cls-2 {
        fill: #35b3d4 !important; }
  .topNavigationRoot .title {
    opacity: 0.75;
    font-size: 20px;
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.2;
    letter-spacing: normal;
    text-align: left;
    color: #ffffff;
    margin-right: 22px; }
  .topNavigationRoot .leftButtons {
    float: left;
    display: flex;
    flex-direction: row; }
    .topNavigationRoot .leftButtons .homeLinks {
      display: flex;
      flex-direction: row; }
      .topNavigationRoot .leftButtons .homeLinks .link {
        opacity: 0.5;
        margin-right: 22px;
        font-size: 11px;
        font-weight: bold;
        width: 36.6px;
