        cursor: pointer; }
      .topNavigationRoot .rightButtons .homeButtons svg:hover {
        opacity: 1; }
      .topNavigationRoot .rightButtons .homeButtons .bp3-popover-wrapper {
        height: 12px; }
      .topNavigationRoot .rightButtons .homeButtons .leftButton {
        margin-right: 15px;
        margin-bottom: 4px;
