      .topNavigationRoot .rightButtons .homeButtons .sb-avatar {
        margin-right: 12px; }
      .topNavigationRoot .rightButtons .homeButtons .emailAddress {
        color: white;
        opacity: 0.5;
        font-size: 11px;
        margin-bottom: 4px;
        margin-top: auto; }
      .topNavigationRoot .rightButtons .homeButtons .button {
        margin-right: 22px; }
      .topNavigationRoot .rightButtons .homeButtons .loginButton {
        border: none; }
      .topNavigationRoot .rightButtons .homeButtons .vr {
        margin-right: 10px;
        margin-top: 4px;
        height: 12px; }
  .topNavigationRoot .vr {
    border-right: 1px solid rgba(255, 255, 255, 0.5);
    height: 14px;
    margin-right: 20px;
    margin-top: 8px; }
