.timeLineWrapper {
  display: flex;
  flex-direction: column;
  color: white;
  font-family: Roboto, -apple-system, BlinkMacSystemFont, 'Segoe UI', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; }
  .timeLineWrapper .headerRow {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 38px; }
    .timeLineWrapper .headerRow .timeLineTitle {
      font-size: 12px;
      font-weight: bold; }
    .timeLineWrapper .headerRow .zoomSliderWrapper {
      width: 110px;
      height: 25px;
      display: flex;
      flex-direction: column; }
      .timeLineWrapper .headerRow .zoomSliderWrapper .notches {
        height: 10px; }
        .timeLineWrapper .headerRow .zoomSliderWrapper .notches .notch {
          border-left: 1px solid white;
          opacity: 0.35; }
      .timeLineWrapper .headerRow .zoomSliderWrapper .zoomSlider {
        height: 15px;
        border-radius: 10px;
        background-color: rgba(34, 100, 144, 0.5); }
        .timeLineWrapper .headerRow .zoomSliderWrapper .zoomSlider .rc-slider-mark {
          top: -14px; }
          .timeLineWrapper .headerRow .zoomSliderWrapper .zoomSlider .rc-slider-mark .rc-slider-mark-text {
            user-select: none;
            color: white;
            opacity: 0.35;
            font-size: 10px; }
        .timeLineWrapper .headerRow .zoomSliderWrapper .zoomSlider .rc-slider-rail {
          opacity: 0; }
        .timeLineWrapper .headerRow .zoomSliderWrapper .zoomSlider .rc-slider-track {
          opacity: 0; }
        .timeLineWrapper .headerRow .zoomSliderWrapper .zoomSlider .rc-slider-step .rc-slider-dot {
          opacity: 0;
          cursor: default; }
        .timeLineWrapper .headerRow .zoomSliderWrapper .zoomSlider .rc-slider-handle {
          cursor: default;
          border: 2px solid white;
          background-color: #226490; }
  .timeLineWrapper .dateSliderWrapper {
    height: 15px;
    display: flex;
    flex-direction: column;
    background-color: rgba(34, 100, 144, 0.5);
    padding-left: 15px;
    padding-right: 15px;
    border-radius: 10px; }
    .timeLineWrapper .dateSliderWrapper .rc-slider-disabled {
      background-color: unset;
      opacity: 0.25; }
    .timeLineWrapper .dateSliderWrapper .dateSlider {
      height: 15px; }
      .timeLineWrapper .dateSliderWrapper .dateSlider .rc-slider-mark {
        top: -20px; }
        .timeLineWrapper .dateSliderWrapper .dateSlider .rc-slider-mark .rc-slider-mark-text {
          user-select: none;
          color: white;
          opacity: 0.35;
          font-size: 10px; }
      .timeLineWrapper .dateSliderWrapper .dateSlider .rc-slider-rail {
        opacity: 0; }
      .timeLineWrapper .dateSliderWrapper .dateSlider .rc-slider-track {
        background-color: #226490; }
      .timeLineWrapper .dateSliderWrapper .dateSlider .rc-slider-step .rc-slider-dot {
        cursor: default;
        background-color: #ffffff;
        opacity: 0.2;
        border: none; }
      .timeLineWrapper .dateSliderWrapper .dateSlider .rc-slider-handle {
        cursor: default;
        border: 2px solid white;
        background-color: #226490; }
