.tabbedPanelWrapper .tabbedPanel {
  color: white; }
  .tabbedPanelWrapper .tabbedPanel .bp3-tab-list {
    width: 100%; }
    .tabbedPanelWrapper .tabbedPanel .bp3-tab-list .bp3-tab {
      display: flex;
      flex-direction: column;
      color: white;
      background-color: rgba(34, 100, 144, 0.3) !important;
      opacity: 0.5;
      width: 38px;
      height: 38px;
      margin-right: 0px !important;
      justify-content: center;
      align-content: center;
      align-items: center;
      text-align: center; }
      .tabbedPanelWrapper .tabbedPanel .bp3-tab-list .bp3-tab svg {
        width: 15px;
        height: 15px;
        fill: white !important; }
    .tabbedPanelWrapper .tabbedPanel .bp3-tab-list .bp3-tab:focus {
      outline: none; }
    .tabbedPanelWrapper .tabbedPanel .bp3-tab-list .bp3-tab[aria-selected="true"] {
      opacity: 1; }
    .tabbedPanelWrapper .tabbedPanel .bp3-tab-list .extraTabContent {
      opacity: 0.5;
      display: flex;
      flex-direction: row;
      margin-bottom: 10px; }
      .tabbedPanelWrapper .tabbedPanel .bp3-tab-list .extraTabContent div {
        display: flex;
        flex-direction: row; }
  .tabbedPanelWrapper .tabbedPanel .bp3-tab-panel {
    background-color: rgba(34, 100, 144, 0.3);
    border-radius: 3.5%;
    border-top-left-radius: 0px;
    margin-top: 0px; }
    .tabbedPanelWrapper .tabbedPanel .bp3-tab-panel .wrapper {
      width: 100%;
      height: 100%;
      display: flex;
      flex-grow: 1;
      justify-content: center;
      align-items: center; }
  .tabbedPanelWrapper .tabbedPanel .bp3-tab-indicator-wrapper {
    display: none; }
