.viewDocument .contentWrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  background-color: rgba(34, 100, 144, 0.3);
  border-radius: 0.5em;
  border-top-left-radius: 0;
  justify-content: center;
  margin-bottom: 0px;
  overflow-y: scroll; }

.viewDocument .viewDocumentHeader {
  margin-right: auto;
  margin-left: 30px;
  display: flex;
  height: 18px;
  margin-top: 30px;
  margin-bottom: 12px;
  display: flex;
  flex-direction: row;
  font-size: 12px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.17;
  letter-spacing: 0.1px;
  text-align: left;
  color: #ffffff; }

.viewDocument .viewDocumentWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  width: 100%;
  height: 100%;
  border-radius: 3.5%;
  border-top-left-radius: 0px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  overflow: scroll; }
  .viewDocument .viewDocumentWrapper .pdf {
    height: 100%;
    width: 100%;
    padding-top: 0px;
    overflow-y: scroll; }
    .viewDocument .viewDocumentWrapper .pdf #sidebar {
      display: none; }
    .viewDocument .viewDocumentWrapper .pdf #page-container {
      position: relative; }
      .viewDocument .viewDocumentWrapper .pdf #page-container .pf {
        margin-top: 20px; }
        .viewDocument .viewDocumentWrapper .pdf #page-container .pf .pc {
          padding: 0px; }
          .viewDocument .viewDocumentWrapper .pdf #page-container .pf .pc .bi {
            margin-bottom: 8px; }
  .viewDocument .viewDocumentWrapper .docx {
    overflow-y: scroll;
    background-color: white;
    padding: 20px; }
    .viewDocument .viewDocumentWrapper .docx p,
    .viewDocument .viewDocumentWrapper .docx h1,
    .viewDocument .viewDocumentWrapper .docx h2,
    .viewDocument .viewDocumentWrapper .docx h3,
    .viewDocument .viewDocumentWrapper .docx h4 {
      color: black;
      width: 100%;
      word-break: break-word; }
    .viewDocument .viewDocumentWrapper .docx a {
      color: #31b2d4; }
  .viewDocument .viewDocumentWrapper .default {
    padding-top: 0px;
    overflow-y: scroll;
    align-self: flex-start;
    width: 100%;
    height: 100%;
    padding-bottom: 80px; }
    .viewDocument .viewDocumentWrapper .default div {
      height: 100%; }
      .viewDocument .viewDocumentWrapper .default div pre {
        height: 100%; }
  .viewDocument .viewDocumentWrapper .png img {
    max-width: 100%;
    min-width: 75%;
    max-height: 100%;
    min-height: 75%; }
  .viewDocument .viewDocumentWrapper .svg {
    width: 75%;
    height: 75%; }
  .viewDocument .viewDocumentWrapper .email {
    width: 100%;
    height: 100%;
    padding: 20px;
    overflow: scroll;
    color: black;
    background-color: white; }
    .viewDocument .viewDocumentWrapper .email a {
      color: blue; }
  .viewDocument .viewDocumentWrapper .emailExtras {
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
    align-self: flex-start;
    flex-grow: 1;
    width: 100%; }
    .viewDocument .viewDocumentWrapper .emailExtras div {
      background-color: rgba(34, 100, 144, 0.6);
      border-radius: 0.5em;
      padding: 5px;
      padding-left: 10px;
      display: flex;
      flex-direction: row;
      align-content: center;
      margin-bottom: 10px;
      width: 100%;
      height: 60px; }
      .viewDocument .viewDocumentWrapper .emailExtras div .emailLabel {
        min-width: 90px;
        font-weight: bold; }
      .viewDocument .viewDocumentWrapper .emailExtras div .value {
        overflow: scroll;
        white-space: pre-wrap;
        width: 100%; }
      .viewDocument .viewDocumentWrapper .emailExtras div span {
        display: flex;
        align-self: center; }
