.proofDiagramWrapper {
  background-color: #2f79a3;
  width: 100%;
  height: 100%;
  flex-grow: 4;
  overflow-y: scroll;
  min-height: 500px;
  min-width: 1180px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 195px;
  padding-right: 195px;
  padding-top: 12px;
  padding-bottom: 12px; }
  .proofDiagramWrapper .row {
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
    .proofDiagramWrapper .row .column {
      width: 25%; }
  .proofDiagramWrapper .rowOne {
    text-align: center; }
  .proofDiagramWrapper .rowTwo {
    flex-grow: 1;
    min-height: 260px; }
    .proofDiagramWrapper .rowTwo .selector {
      width: 100%;
