      margin-right: 7px; }
    .proofDiagramWrapper .rowThree .first {
      margin-left: 95px; }
    .proofDiagramWrapper .rowThree .hr {
      border-bottom: 1px solid white;
      margin-bottom: 10px; }
    .proofDiagramWrapper .rowThree .hrOne {
      width: 170px; }
    .proofDiagramWrapper .rowThree .hrTwo {
      width: 171px; }
    .proofDiagramWrapper .rowThree .hrThree {
      width: 170px; }
